
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineCalendar from 'vux/src/components/inline-calendar/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'






import {Meeting} from '../../service/getData'

export default {
mounted :　function(){
var myDate = new Date();
var months = myDate.getMonth() + 1;//获取当前月份(0-11,0代表1月)
},
methods:{
onChange (val) {
var nowTime = val;
var nowTime = nowTime.split("-");
Meeting().then(reponse => {

this.MeetingList = reponse.data;
}).catch(e)(error => {

})
},
onViewChange (val, count) {
console.log('on view change', val, count)
}
},
data () {
return {
show: true,
value: '',
listValue: '',
range: false,
showLastMonth: true,
showNextMonth: true,
highlightWeekend: false,
return6Rows: true,
hideHeader: false,
hideWeekList: false,
replaceTextList: {},
replace: false,
changeWeeksList: false,
weeksList: [],
useCustomFn: false,
buildSlotFn: () => '',
disablePast: false,
disableFuture: false,
MeetingList: []
}
},
watch: {
replace (val) {
this.replaceTextList = val ? {
'TODAY': '今'
} : {}
},
useCustomFn (val) {
this.buildSlotFn = val ? (line, index, data) => {
return /8/.test(data.date) ? '<div style="font-size:12px;text-align:center;"><span style="display:inline-block;width:5px;height:5px;background-color:red;border-radius:50%;"></span></div>' : '<div style="height:19px;"></div>'
} : () => ''
},
changeWeeksList (val) {
this.weeksList = val ? ['日', '一', '二', '三', '四', '五', '六 '] : ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa']
}
},
components:{
XHeader,
InlineCalendar,
Group,
Cell
}
}



